<template>
  <div class="myManForm">
      <!---->
      <ul class="formList">
        <template v-for="(item,index) in formArry">
          <input type="hidden" :name="item.fieldCode" value=""/>
          <!---->
          <li v-if="item.fieldType.typeId==0" @click="getItemInfo(index,item.fieldCode,item.fieldType.typeId,item.fieldType.content)">
            <span></span>
            <input type="text" v-model="staticInp" readonly="readonly"/>
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li v-if="item.fieldType.typeId==1" @click="getItemInfo(index,item.fieldCode,item.fieldType.typeId,item.fieldType.content)">
            <span></span>
            <input type="number" v-model="numInp" />
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li v-if="item.fieldType.typeId==2" @click="getItemInfo(index,item.fieldCode,item.fieldType.typeId,item.fieldType.content)">
            <span></span>
            <input type="text" v-model="aSingleInp" />
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li v-if="item.fieldType.typeId==3" @click="getItemInfo(index,item.fieldCode,item.fieldType.typeId,item.fieldType.content)">
            <span></span>
            <p>{{supplementText}}</p>
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li @click="openPicker" v-if="item.fieldType.typeId==4">
            <span></span>
            <p>{{dateVal}}</p>
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li @click="openPickerLength" v-if="item.fieldType.typeId==5">
            <span></span>
            <p>{{timeVal}}</p>
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li @click="showOrClosePop(6,'itemFlag')" v-if="item.fieldType.typeId==6">
            <span></span>
            <p>{{radioVal}}</p>
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li v-if="item.fieldType.typeId==7" @click="getItemInfo(index,item.fieldCode,item.fieldType.typeId,item.fieldType.content)">
            <span></span>
            <select>
              <option value="1">abc</option>
              <option value="2">bac</option>
              <option value="3">cab</option>
            </select>
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>
          <!---->
          <li @click="showOrClosePop(8,'itemFlag')" v-if="item.fieldType.typeId==8">
            <span></span>
            <p>{{checkBoxStr}}</p>
            <img src="../../assets/images/manage/rightarrowicon@2x.png" alt="">
          </li>

        </template>
      </ul>
    <!---->
    <div class="subFormBtn" @click="submitForm()"></div>
<!--

-->
      <div class="pop_background" @click="showOrClosePop(0)" v-if="popStatus==3||popStatus==6||popStatus==8"></div>
      <!----->
      <div class="pop_supplement" v-if="popStatus==3">
        <p class="popsup_title"></p>
        <textarea placeholder="" v-model="supplementPopText"></textarea>
        <div class="popsup_btn pop_btn">
          <span @click="showOrClosePop(0)"></span>
          <span @click="supplementFn"></span>
        </div>
      </div>
      <!----->
      <div class="pop_checkBox" v-if="popStatus==8">
        <mt-checklist
          v-model="checkBoxVal"
          :options="checkBoxPop">
        </mt-checklist>
        <div class="popOmen_btn pop_btn">
          <span @click="showOrClosePop(0)"></span>
          <span @click="supplementFn"></span>
        </div>
      </div>
      <!----->
      <div class="pop_checkBox pop_radio" v-if="popStatus==6">
        <mt-radio
          v-model="radioVal"
          :options="radioPop">
        </mt-radio>
        <div class="popOmen_btn pop_btn">
          <span @click="showOrClosePop(0)"></span>
          <span @click="supplementFn"></span>
        </div>
      </div>
      <!--datetime-->
      <mt-datetime-picker
        ref="dateTimeType"
        type="datetime"
        v-model="dateVal"
        year-format="{value} "
        month-format="{value} "
        date-format="{value} "
        hour-format="{value}"
        minute-format="{value}"
        @confirm="handleConfirm"
        :startDate="startDate">
      </mt-datetime-picker>
      <!--time-->
      <mt-datetime-picker
        ref="timeType"
        type="time"
        v-model="timeVal"
        hour-format="{value}"
        minute-format="{value}"
        @confirm="handleConfirmLength">
      </mt-datetime-picker>

  </div>
</template>
<script>
  import moment from 'moment'// 
  import {getPaperStruct,getPaperList} from '@/api/data/index.js' //
export default {
    props:["formArry"],
    data () {
        return {
          paperId : '',
          fieldList : [],     //
          supplementText : '',//
          supplementPopText : '',//
          startDate: new Date('1807'),//
          //endDate: new Date('2018'),//
          radioPop:[],//
          checkBoxPop:[],//
          checkBoxVal:[],//

          itemIndex : 0,//===========

          popStatus : 0,//(0,6,8,3)
          subObj:{},//
          staticInp:'',//0.
          numInp:null,//1.
          aSingleInp:'',//2.
          textareaVal:'',//3.
          dateVal: '', //4.
          timeVal: '',//5.
          radioVal:'',//6.
          checkBoxStr: '',//8.




          /**/
          itemIndex:null,
          itemFieldCode:'',
          itemTypeId:null,
          itemContent:'',






        }
    },
    mounted(){
        this.checkList();
    },
    methods : {
      //
      getItemInfo(index,fieldCode,typeId,content){//:1.,2.key,3.,4.
        this.itemIndex=index;
        this.itemFieldCode=fieldCode;
        this.itemTypeId=typeId;
        this.itemContent=content;
        console.log('',this.itemIndex+'/'+this.itemFieldCode+'/'+this.itemTypeId+'/'+this.itemContent)

        switch (typeId){
            //
          case 0:
            this.subObj[fieldCode]=this.staticInp;
            break;
          //
          case 1:
            this.subObj[fieldCode]=this.numInp;
              break;
          //
          case 2:
            this.subObj[fieldCode]=this.aSingleInp;




            this.popStatus=3;


            break;
          //
          case 3:

            break;
          //
          case 4:
            break;
          //
          case 5:
            break;
          //
          case 6:
            break;
          //
          case 7:
            break;
          //
          case 8:
            break;

        }
        console.log(this.subObj)
      },
      //
      submitForm(){

      },

      //
      showOrClosePop(popFlag,index){

        if(index==1){
            this.itemIndex=index;
        }
        this.popStatus=popFlag;

      },
      //pop
      supplementFn(){
        this.popStatus=0;//

        this.supplementText=this.supplementPopText;
        this.checkBoxStr = this.checkBoxVal.join(",");
        //alert(this.itemTypeId+","+this.itemFieldCode+","+this.supplementText)

        if(this.itemTypeId==3){
          this.subObj[this.itemFieldCode]=this.supplementText;
        }else if(this.itemTypeId==6){
          this.subObj[this.itemFieldCode]=this.radioVal;
        }else if(this.itemTypeId==8){
          this.subObj[this.itemFieldCode]=this.checkBoxStr;
        }

        console.log(this.subObj)
      },
      //datetime
      openPicker () {
        this.$refs.dateTimeType.open()
        this.dateVal=new Date()
      },
      handleConfirm (data) {//
        let date = moment(data).format("YYYY-MM-DD HH:mm")
        this.dateVal = date
      },
      //time
      openPickerLength(){
        this.$refs.timeType.open()
      },
      handleConfirmLength(data){//
        this.timeVal = data
      },
      //
      checkList(){
        this.checkBoxPop = [
          {
            label: '',
            value: 'F',
          },
          {
            label: '',
            value: '',
          },
          {
            label: 'A',
            value: 'A'
          },
          {
            label: 'B',
            value: 'B'
          },
          {
            label: 'A',
            value: 'A1'
          },
          {
            label: 'B',
            value: 'B2'
          },
          {
            label: '',
            value: '3',
          },
          {
            label: 'A',
            value: 'A4'
          },
          {
            label: 'B',
            value: 'B5'
          },
          {
            label: '',
            value: '6',
          },
          {
            label: 'A',
            value: 'A7'
          },
          {
            label: 'B',
            value: 'B8'
          }

        ];
        this.radioPop = [
          {
            label: '',
            value: 'F',
          },
          {
            label: '',
            value: '',
          },
          {
            label: 'A',
            value: 'A'
          },
          {
            label: 'B',
            value: 'B'
          },
          {
            label: 'A',
            value: 'A1'
          },
          {
            label: 'B',
            value: 'B2'
          },
          {
            label: '',
            value: '3',
          },
          {
            label: 'A',
            value: 'A4'
          },
          {
            label: 'B',
            value: 'B5'
          },
          {
            label: '',
            value: '6',
          },
          {
            label: 'A',
            value: 'A7'
          },
          {
            label: 'B',
            value: 'B8'
          }

        ];
      },




    }
}
</script>
<style lang="scss">
  /**/
  .picker-toolbar {
    height: 88px;
  }
  .mint-datetime-action {
    line-height: 88px;
    font-size: 34px;
  }
  .picker-slot {
    font-size: 34px;
  }
  .picker-items {
    height: 400px;
  }
  .mint-datetime-cancel {
    color: #333;
  }

  .myManForm{
    width: 750px;
    background: #fff;
    height: 1334px;
    overflow-y: auto;
    padding-top: 80px;
    padding-bottom: 190px;
    .subFormBtn{
      width: 700px;
      height: 88px;
      line-height: 88px;
      background: #2B8CFF;
      border-radius: 4px;
      font-family: PingFangSC-Regular;
      font-size: 34px;
      color: #FFF;
      text-align: center;
      position: absolute;
      bottom: 80px;
      left: 0px;
      right: 0px;
      margin: auto;
    }
    .formList{
        li{
            display: flex;
            align-items: center;
            flex-direction: row;
            width: 100%;
            height: 110px;
            border-bottom: 1px solid #eee;
            padding: 0 25px;
            padding-bottom: 1px;
            font-size: 34px;
              span{
                width: 150px;
                color: #333333;
                text-align: center;
                margin-right: 30px;
              }
              input,select{
                flex: 1;
                font-size: 34px;
                color: #888888;
                text-align: right;
                border: none;
              }
              select {
                direction: rtl;
              }
              select option {
                direction: ltr;
              }
              p{
                flex: 1;
                font-size: 34px;
                color: #888888;
                border:none;
                line-height: 52px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                text-align: right;
              }
              img{
                width: 14px;
                height: 24px;
                margin-left: 30px;
              }
          }
        li:nth-child(1){
          border-top: 1px solid #eee;
        }
      }
    .pop_background{
        width: 100%;
        height: 100%;
        opacity: 0.5;
        background: #000;
        position: fixed;
        top: 0;
      }
    .pop_supplement{
        width: 600px;
        height: 460px;
        background: #FFFFFF;
        border-radius: 10px;
        position: fixed;
        left: 0px;
        right: 0px;
        bottom: 0px;
        top: 0px;
        margin: auto;
        font-size: 34px;
        color: #333333;
        text-align: center;
          .popsup_title{
            font-size: 34px;
            color: #333333;
            line-height: 108px;
            text-align: center;
          }
          textarea{
            width: 540px;
            height: 234px;
            background: #F2F4F5;
            font-size: 34px;
            color: #333333;
          }
      }
    .pop_checkBox{
        position: fixed;
        left: 0px;
        right: 0px;
        bottom: 0px;
        top: 0px;
        margin: auto;
        width: 600px;
        height: 948px;
        background: #FFF;
        border-radius: 10px;
        padding-top: 54px;
        .mint-radiolist{
          width: 100%;
          height: 806px;
          overflow-y: auto;
          .mint-cell{
            width: 100%;
            padding: 0px 75px 48px 75px;
            .mint-radio-core{
              width: 40px;
              height: 40px;
              background: #EEE;
              border: none;
              vertical-align: sub;
            }
            .mint-radio-core::after {
              width: 16px;
              height: 16px;
              left: 12px;
              top:12px;
            }
            .mint-radio-input:checked + .mint-radio-core {
              background-color: #26a2ff;
              border-color: #26a2ff;
            }
            label{
              font-size: 34px;
              color: #333333;
            }
          }
        }
        .mint-checklist{
            width: 100%;
            height: 806px;
            overflow-y: auto;
              .mint-cell{
                width: 100%;
                padding: 0px 75px 48px 75px;
                .mint-checkbox-core{
                  width: 40px;
                  height: 40px;
                  border-radius: 2px;
                  background: #EEEEEE;
                  border: none;
                  vertical-align: sub;
                }
                .mint-checkbox-core::after {
                  width: 12px;
                  height: 24px;
                  top:0px;
                  left: 12px;
                }
                .mint-checkbox-input:checked + .mint-checkbox-core {
                  background-color: #26a2ff;
                  border-color: #26a2ff;
                }
                label{
                  font-size: 34px;
                  color: #333333;
                }
              }
          }

      }
    .pop_btn{
      width: 100%;
      height: 88px;
      line-height: 88px;
      display: flex;
      justify-content: space-around;
      border-top: 1px solid #eee;
      position: absolute;
      bottom: 0px;
      background: #fff;
      z-index: 2;
      span{
        text-align: center;
        font-size: 34px;
      }
      span:nth-child(2){
        color: #2B8CFF;
      }
    }
  }
</style>





